import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { MaintenanceService } from "../services/maintenanceService";
import swal from 'sweetalert2';

declare interface DataTable {
    mTanceHeaderRow: string[];
    mTanceFooterRow: string[];
    mTanceDataRows: any;
}
declare const $: any;
declare const jQuery: any;

@Component({
    selector: 'app-maintenance',
    templateUrl: './maintenance.component.html',
    styleUrls: ['./maintenance.component.scss'],
    providers: [CommonService, MaintenanceService]
})
export class MaintenanceComponent implements OnInit {
    public mTanceDataTable: DataTable;
    public maintenanceList: any;
    public isViewAccess: boolean;
    public isAddAccess: boolean;
    public isEditAccess: boolean;
    public isSyncAccess: boolean;
    public userProfile: any;
    public mTypeList: any;
    public mPartTypeList: any;
    public mtClientList: any;
    public mtRaisedByList: any;
    public assUserID: any;

    public mTanceForm = new FormGroup({
        trackChk: new FormControl(),
        checkAll: new FormControl(),
    });

    public mtForm = new FormGroup({
        mtClient: new FormControl(),
        mType: new FormControl(),
        mPartType: new FormControl(),
        mStatus: new FormControl(),
        mtRaisedBy: new FormControl(),
    });

    public mtComPage = new FormGroup({
        comDesc: new FormControl(),
    });

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private maintenanceService: MaintenanceService,
        private activatedRoute: ActivatedRoute,
        private formBuilder: FormBuilder,
    ) { }

    isFieldValid(form: FormGroup, field: string) {
        return !form.get(field).valid && form.get(field).touched;
    }

    displayFieldCss(form: FormGroup, field: string) {
        return {
            'has-error': this.isFieldValid(form, field),
            'has-feedback': this.isFieldValid(form, field)
        };
    }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));
            let userRoleAccess = JSON.parse(sessionStorage.getItem('AccessRole'));
            if (userRoleAccess != null) {
                var jobAccess = userRoleAccess.filter(job => job.ModuleName == "MAINTENANCE");
                if (jobAccess != null) {
                    this.isViewAccess = (jobAccess[0].AddAccess == true || jobAccess[0].EditAccess == true ? true : jobAccess[0].ViewAccess);
                    this.isAddAccess = jobAccess[0].AddAccess;
                    this.isEditAccess = jobAccess[0].EditAccess;
                    this.isSyncAccess = jobAccess[0].SyncAccess;
                }
            }
            this.getMaintenanceType();
            this.getMaintenancePartType();
            this.getMaintenanceDetails();
            this.getClientForMaintenance();
            this.getRaisedByForMaintenance();
            this.mTanceDataTable = {
                mTanceHeaderRow: ['MT#', 'Work Order', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],
                mTanceFooterRow: ['MT#', 'Work Order', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],
                mTanceDataRows: this.maintenanceList
            };
            $(document).ready(function () {
                $("#chkAll").click(function () {
                    $('input:checkbox').not(':disabled').prop('checked', this.checked);
                    //$('input:checkbox').not(this).prop('checked', this.checked);
                });
            });

            this.mtForm = this.formBuilder.group({
                mType: null,
                mPartType: null,
                mStatus: null,
                mtClient: null,
                mtRaisedBy: null
            });

            this.mtComPage = this.formBuilder.group({
                comDesc: ['', Validators.required]
            });

        } else {
            this.router.navigate(['pages/login']);
        }
    }

    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }

    approvedMaintenance(status: any) {
        $('.loader').show();
        var chckeValue = [];
        $('input[name="trackChk"]:checked').each(function () {
            chckeValue.push(this.id);
        });
        if (chckeValue.length > 0) {
            //this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));

            var contact = {
                Status: status
            };

            this.maintenanceService.UpdateMaintenanceStatus(this.commonService.baseApiUrl, chckeValue, contact).subscribe(
                data => {
                    var datatSheet = JSON.stringify(data);
                    var tSheetParse = JSON.parse(datatSheet);
                    var tSheetList = JSON.parse(tSheetParse["_body"]);
                    if (tSheetList != null) {
                        $('.loader').hide();
                        this.mTanceForm.reset();
                        this.getMaintenanceDetails();
                    }
                });
        }
        else {
            $('.loader').hide();
        }
    }
    resetMaintenance() {
        this.mtComPage.reset();
    }

    getMaintenanceDetails() {
        var contact = {
            ClientName: this.mtForm.controls["mtClient"].value != -1 ? this.mtForm.controls["mtClient"].value : null,
            MTypeID: this.mtForm.controls["mType"].value != -1 ? this.mtForm.controls["mType"].value : 0,
            MPartTypeID: this.mtForm.controls["mPartType"].value != -1 ? this.mtForm.controls["mPartType"].value : 0,
            RaisedBy: this.assUserID != null ? this.assUserID : this.mtForm.controls["mtRaisedBy"].value,
            Status: this.mtForm.controls["mStatus"].value != -1 ? this.mtForm.controls["mStatus"].value : null
        };
        $('.loader').show();
        this.maintenanceService.GetMaintenanceDetails(this.commonService.baseApiUrl, contact).subscribe(
            data => {
                var dataMtance = JSON.stringify(data);
                var mtanceParse = JSON.parse(dataMtance);
                var mTanceList = JSON.parse(mtanceParse["_body"]);
                this.maintenanceList = null;
                if (mTanceList != null) {
                    $("#mTanceDatatables").dataTable().fnDestroy();
                    this.maintenanceList = mTanceList;

                    this.mTanceDataTable = {
                        mTanceHeaderRow: ['MT#', 'Work Order', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],//'ID'
                        mTanceFooterRow: ['MT#', 'Work Order', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],
                        mTanceDataRows: this.maintenanceList
                    };

                    setTimeout(() => {
                        $("#mTanceDatatables").dataTable().fnDestroy();
                        $('#mTanceDatatables').DataTable({
                            'pagingType': 'full_numbers',
                            'lengthMenu': [[10, 25, 50, -1], [10, 25, 50, 'All']],
                            responsive: true,
                            language: {
                                search: '_INPUT_',
                                searchPlaceholder: 'Search records',
                            }

                        });

                        const tableWO = $('#mTanceDatatables').DataTable();

                        // Edit record
                        tableWO.on('click', '.edit', function () {

                        });

                        // Delete a record
                        tableWO.on('click', '.remove', function (e: any) {
                            const $trWO = $(this).closest('tr');
                            tableWO.row($trWO).remove().draw();
                            e.preventDefault();
                        });

                        // Like record
                        tableWO.on('click', '.like', function () {
                            alert('You clicked on Like button');
                        });

                    }, 10);
                    //add new
                    jQuery.fn.dataTable.moment('DD/MM/YYYY');
                    //add end
                    $('.loader').hide();
                }
            }
        );
    }

    getMaintenanceType() {
        $('.loader').show();
        return this.maintenanceService.GetActiveMaintenanceType(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMtp = JSON.stringify(data);
                var mTpParse = JSON.parse(dataMtp);
                var mTpList = JSON.parse(mTpParse["_body"]);
                if (mTpList != null) {
                    this.mTypeList = mTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    getMaintenancePartType() {
        $('.loader').show();
        return this.maintenanceService.GetActiveMaintenancePartType(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMptp = JSON.stringify(data);
                var mpTpParse = JSON.parse(dataMptp);
                var mpTpList = JSON.parse(mpTpParse["_body"]);
                if (mpTpList != null) {
                    this.mPartTypeList = mpTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    getClientForMaintenance() {
        $('.loader').show();
        return this.maintenanceService.GetClientForMaintenance(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                var dataMptp = JSON.stringify(data);
                var mpTpParse = JSON.parse(dataMptp);
                var mpTpList = JSON.parse(mpTpParse["_body"]);
                if (mpTpList != null) {
                    this.mtClientList = mpTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    getRaisedByForMaintenance() {
        $('.loader').show();
        return this.maintenanceService.GetRaisedByForMaintenance(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                var dataMptp = JSON.stringify(data);
                var mpTpParse = JSON.parse(dataMptp);
                var mpTpList = JSON.parse(mpTpParse["_body"]);
                if (mpTpList != null) {
                    this.mtRaisedByList = mpTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    public myListFormatter(data: any): string {
        return data['UserName'];
    }

    valueChanged(newVal) {
        $(".cntEmpty").removeClass("is-empty");
        this.assUserID = newVal.ID;
        this.getMaintenanceDetails();
    }

    goToCreateMaintenance(mtId: any) {
        this.router.navigate(['create-maintenance'], { queryParams: { mtid: mtId } });
    }

}



